export const omit = (obj, ...keys) => {
    const result = { ...obj };
    for (const key of keys) {
        delete result[key];
    }
    return result;
};
export const mergeRefs = (refs) => {
    return (value) => {
        refs.filter(Boolean).forEach((ref) => {
            if (typeof ref === "function") {
                ref(value);
            }
            else if (ref != null) {
                ref.current = value;
            }
        });
    };
};
